import React from "react";

import { Paper } from "@material-ui/core";
import KeyboardArrowUpIcon from "@material-ui/icons/KeyboardArrowUp";
import KeyboardArrowDownIcon from "@material-ui/icons/KeyboardArrowDown";
import RemoveIcon from "@material-ui/icons/Remove";

export default function(props) {
  let { title, number, delta } = props;
  let colors = ["#00cc00", "grey", "red"];
  let index = 1;
  if (delta < 0) {
    index = 0;
  } else if (delta > 0) {
    index = 2;
  }

  return (
    <Paper className="paperPadding" style={{ textAlign: "center" }}>
      <h3>{title}</h3>
      <b className="numberText">{number}</b>
      {delta !== undefined && index == 0 && (
        <KeyboardArrowDownIcon
          className="deltaIcon"
          style={{ color: colors[index] }}
        />
      )}
      {delta !== undefined && index == 1 && (
        <RemoveIcon className="deltaIcon" style={{ color: colors[index] }} />
      )}
      {delta !== undefined && index == 2 && (
        <KeyboardArrowUpIcon
          className="deltaIcon"
          style={{ color: colors[index] }}
        />
      )}
      {delta !== undefined && (
        <strong className="deltaText" style={{ color: colors[index] }}>
          {delta}
        </strong>
      )}
    </Paper>
  );
}
