import React from "react";

import { Grid } from "@material-ui/core";

import NumberCard from "../NumberCard";

export default function(props) {
  let latest = props.dailyData[props.dailyData.length - 1];
  let delta = props.dailyData[props.dailyData.length - 2];
  return (
    <>
      <Grid item xs={4}>
        <NumberCard
          title="Hospitalised Cases"
          number={latest.HospitalisedCovidCases}
          delta={latest.HospitalisedCovidCases - delta.HospitalisedCovidCases}
        />
      </Grid>
      <Grid item xs={4}>
        <NumberCard
          title="ICU Cases"
          number={latest.RequiringICUCovidCases}
          delta={latest.RequiringICUCovidCases - delta.RequiringICUCovidCases}
        />
      </Grid>
      <Grid item xs={4}>
        <NumberCard title="Median Age" number={latest.Median_Age} />
      </Grid>
    </>
  );
}
