import React from "react";

import L from "leaflet";
import { Map, TileLayer, GeoJSON } from "react-leaflet";

import { max, scaleLinear, scaleSqrt } from "d3";

const CountyMap = (props) => {
  let colorScale = scaleLinear()
    .domain([
      0,
      max(props.geoData, (d) => d.properties.PopulationProportionCovidCases),
    ])
    .range(["green", "red"]);

  let radiusScale = scaleSqrt()
    .domain([
      0,
      max(props.geoData, (d) => d.properties.PopulationProportionCovidCases),
    ])
    .range([0, 20000]);

  const position = [53.2632, -7.6607];

  let onEachFeature = (feature, layer) => {
    const popupContent = ` <Popup className='mapPopup'># ${feature.properties.CountyName} Cases: ${feature.properties.ConfirmedCovidCases}</Popup>`;
    layer.bindPopup(popupContent);
  };

  let pointToLayer = (point, center) => {
    return L.circle(center, {
      radius: radiusScale(point.properties.PopulationProportionCovidCases),
      color: colorScale(point.properties.PopulationProportionCovidCases),
    });
  };

  return (
    <Map
      className="map"
      center={position}
      zoom={6}
      minZoom={6}
      maxBounds={[
        [56, -12],
        [51, -4],
      ]}
    >
      <TileLayer
        url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
      />
      <GeoJSON
        data={props.geoData}
        onEachFeature={onEachFeature}
        pointToLayer={pointToLayer}
      />
    </Map>
  );
};

export default CountyMap;
