import React, { useState } from "react";

import { Grid, Paper } from "@material-ui/core";

import CountyChart from "../charts/CountyChart";
import CountyMap from "../map/CountyMap";

export default function(props) {
  let { countyData } = props;
  let [useProportions, setUseProportions] = useState(true);

  return (
    <>
      <Grid item xs={1}></Grid>
      <Grid item xs={5}>
        <Paper className="mapContainer">
          {props.countyData && (
            <CountyMap geoData={countyData} useProportions={useProportions} />
          )}
        </Paper>
      </Grid>
      <Grid item xs={5}>
        <Paper className="mapContainer">
          {props.countyData && (
            <CountyChart
              data={countyData}
              useProportions={useProportions}
              handleToggle={setUseProportions}
            />
          )}
        </Paper>
      </Grid>
      <Grid item xs={1}></Grid>
    </>
  );
}
