import React from "react";

import { Grid, Paper } from "@material-ui/core";

import CasesChart from "../charts/CasesChart";
import AgeProfileChart from "../charts/AgeProfileChart.js";
export default function(props) {
  let { dailyData } = props;
  return (
    <>
      <Grid item xs={1}></Grid>
      <Grid item xs={5}>
        <Paper className="graphContainer">
          {props.dailyData && <CasesChart data={dailyData} />}
        </Paper>
      </Grid>
      <Grid item xs={5}>
        <Paper className="graphContainer">
          {dailyData && <AgeProfileChart data={dailyData} />}
        </Paper>
      </Grid>
      <Grid item xs={1}></Grid>
    </>
  );
}
