import React, { useEffect, useState } from "react";
import { Toolbar, Switch, FormControlLabel } from "@material-ui/core";
import Chart from "chart.js";
import { scaleLinear, max } from "d3";

const fillCountyColor = (context) => {
  return colorScale(context.dataset.data[context.dataIndex]);
  // return Tableau20[context.dataIndex % 20];
};

let colorScale = scaleLinear().range(["green", "red"]);

export default function CountyChart(props) {
  const id = "countyChart";

  let [chart, setChart] = useState();

  const processData = (data) => {
    let values = [];
    let names = [];
    data.forEach((item) => {
      values.push(
        props.useProportions
          ? Math.round(item.properties.PopulationProportionCovidCases)
          : item.properties.ConfirmedCovidCases
      );
      names.push(item.properties.CountyName);
    });
    return { names, values };
  };

  useEffect(() => {
    let { names, values } = processData(props.data);

    colorScale.domain([0, max(values)]);

    setChart(
      new Chart(id, {
        type: "horizontalBar",
        data: {
          labels: names,
          datasets: [
            {
              data: values,
              backgroundColor: fillCountyColor,
            },
          ],
        },
        options: {
          legend: {
            display: false,
          },
          onHover: (e, elements) => {
            console.log(elements);
          },
        },
      })
    );
  }, []);

  useEffect(() => {
    let { names, values } = processData(props.data);

    if (chart != undefined) {
      chart.data = {
        labels: names,
        datasets: [
          {
            data: values,
            backgroundColor: fillCountyColor,
          },
        ],
      };
      chart.update();
    }
  }, [props.useProportions]);

  let handleChange = (e) => {
    props.handleToggle(e.target.checked);
  };

  return (
    <>
      <Toolbar variant="dense">
        <h2 style={{ margin: "auto" }}># Cases per county</h2>
        <FormControlLabel
          control={
            <Switch
              checked={props.useProportions}
              onChange={handleChange}
              name="checkedA"
            />
          }
          label="Per 100k"
        />
      </Toolbar>
      <div style={{ height: "450px", marginTop: "5px" }}>
        <canvas id={id} />
      </div>
    </>
  );
}
