import React, { useEffect } from "react";
import { Toolbar } from "@material-ui/core";
import Chart from "chart.js";
const processData = (data) => {
  let labels = ["Community Transmission", "Close Contact", "Travelling Abroad"];
  let latestEntry = data[data.length - 1];
  let values = [
    latestEntry.CommunityTransmission,
    latestEntry.CloseContact,
    latestEntry.TravelAbroad,
  ];

  return { labels, values };
};

export default function TransmissionChart(props) {
  const id = "transmissionChart";
  useEffect(() => {
    let { labels, values } = processData(props.data);

    new Chart(id, {
      type: "radar",
      data: {
        labels: labels,
        datasets: [
          {
            label: "% of transmissions",
            data: values, //[12, 19, 3, 5, 2, 3]
            backgroundColor: "rgba(0, 120, 255, 0.4)",
            borderColor: "rgba(0, 120, 255, 0.8)",
            pointBackgroundColor: "rgb(0, 120, 255)",
            pointHoverRadius: 5,
          },
        ],
      },
      options: {
        scale: {
          ticks: {
            display: false,
            maxTicksLimit: 8,
          },
        },
        legend: {
          display: false,
        },
      },
    });
  });
  return (
    <>
      <Toolbar variant="dense">
        <h2 style={{ margin: "auto" }}>Cause of transmission</h2>
      </Toolbar>
      <div style={{ height: "240px", marginTop: "10px" }}>
        <canvas id={id} />
      </div>
    </>
  );
}
