import React, { useEffect, useState } from "react";

import moment from "moment";

import { fetchDailyData, fetchCountyDataGeoJson } from "./utils/api.js";
import { Grid, AppBar, Toolbar } from "@material-ui/core";
import TotalCardRow from "./components/layout/TotalCardRow.js";
import CasesCardRow from "./components/layout/CasesCardRow.js";
import SecondChartRow from "./components/layout/SecondChartRow.js";
import CasesAgeRow from "./components/layout/CasesAgeRow.js";
import CountyRow from "./components/layout/CountyRow.js";

export default function() {
  let [dailyData, setDailyData] = useState();
  let [countyData, setCountyData] = useState();

  useEffect(() => {
    fetchDailyData().then((data) => setDailyData(data));
    fetchCountyDataGeoJson().then((data) => setCountyData(data));
  }, []);

  return dailyData && countyData ? (
    <div className="appContainer">
      <AppBar position="static">
        <Toolbar>
          <h2 style={{ marginLeft: "60px" }}>Ireland Covid-19 Statistics</h2>
          <div style={{ float: "right", position: "absolute", right: "10px" }}>
            Daily Data Updated:{" "}
            {moment(dailyData[dailyData.length - 1].Date).format(
              "MMMM Do YYYY h:mm:ss a"
            )}
            <br />
            County Data Updated:{" "}
            {moment(countyData[0].properties.TimeStampDate).format(
              "MMMM Do YYYY h:mm:ss a"
            )}
          </div>
        </Toolbar>
      </AppBar>
      <Grid container spacing={1} className="marginTop5">
        <TotalCardRow dailyData={dailyData} />

        <CasesAgeRow dailyData={dailyData} />

        <CasesCardRow dailyData={dailyData} />

        <CountyRow countyData={countyData} />

        <SecondChartRow dailyData={dailyData} />
      </Grid>
    </div>
  ) : null;
}
