const dailyUrl =
  "https://services1.arcgis.com/eNO7HHeQ3rUcBllm/ArcGIS/rest/services/CovidStatisticsProfileHPSCIrelandView/FeatureServer/0/query?where=1%3D1&outFields=*&f=json";

const countyGeoUrl =
  "https://services1.arcgis.com/eNO7HHeQ3rUcBllm/ArcGIS/rest/services/Covid19CountyStatisticsHPSCIrelandOpenData/FeatureServer/0/query?where=1%3D1&outFields=*&f=geojson";

const fetchData = (url) => fetch(url).then((response) => response.json());

export const fetchDailyData = () =>
  fetchData(dailyUrl).then((data) => {
    console.log(data.features.map((item) => item.attributes));
    return data.features.map((item) => item.attributes);
  });

export const fetchCountyDataGeoJson = () =>
  fetchData(countyGeoUrl).then((data) => {
    let features = data.features;
    let shortlist = [];

    for (let i = features.length - 26; i < features.length; i++) {
      shortlist.push(features[i]);
    }

    console.log(shortlist);
    return shortlist;
  });
