import React, { useEffect } from "react";
import { Toolbar } from "@material-ui/core";
import Chart from "chart.js";

const processData = (data) => {
  let labels = [
    "< 5",
    "5-14",
    "15-24",
    "25-34",
    "35-44",
    "45-44",
    "55-64",
    "65+",
  ];

  let latestEntry = data[data.length - 1];
  let totalsAgeProfile = [
    latestEntry.Aged1 + latestEntry.Aged1to4,
    latestEntry.Aged5to14,
    latestEntry.Aged15to24,
    latestEntry.Aged25to34,
    latestEntry.Aged35to44,
    latestEntry.Aged45to54,
    latestEntry.Aged55to64,
    latestEntry.Aged65up,
  ];
  let hospitalisedAgeProfile = [
    latestEntry.HospitalisedAged5,
    latestEntry.HospitalisedAged5to14,
    latestEntry.HospitalisedAged15to24,
    latestEntry.HospitalisedAged25to34,
    latestEntry.HospitalisedAged35to44,
    latestEntry.HospitalisedAged45to54,
    latestEntry.HospitalisedAged55to64,
    latestEntry.HospitalisedAged65up,
  ];
  return { labels, totalsAgeProfile, hospitalisedAgeProfile };
};

export default function AgeProfileChart(props) {
  let id = "ageProfileChart";
  useEffect(() => {
    let { labels, totalsAgeProfile, hospitalisedAgeProfile } = processData(
      props.data
    );

    new Chart(id, {
      type: "horizontalBar",
      data: {
        labels: labels,
        datasets: [
          {
            label: "# of cases",
            data: totalsAgeProfile,
            backgroundColor: "rgba(0, 120, 255, 0.4)",
            borderColor: "rgba(0, 120, 255, 0.8)",
            borderWidth: 2,
            barThickness: 20,
            order: 1,
          },
          {
            label: "# of hospitalised",
            data: hospitalisedAgeProfile,
            backgroundColor: "rgba(255, 0, 0, 0.4)",
            borderColor: "rgba(255, 0, 0, 0.8)",
            borderWidth: 2,
            barThickness: 30,
            order: 0,
          },
        ],
      },
      options: {
        scales: {
          yAxes: [
            {
              stacked: true,
            },
          ],
        },
      },
    });
  }, []);
  return (
    <>
      <Toolbar variant="dense">
        <h2 style={{ margin: "auto" }}>Age profile breakdown</h2>
      </Toolbar>
      <div style={{ height: "340px", marginTop: "10px" }}>
        <canvas id={id} />
      </div>
    </>
  );
}
