import React, { useEffect } from "react";
import { Toolbar } from "@material-ui/core";
import Chart from "chart.js";

const processData = (data) => {
  let labels = ["Healthcare workers", "Non-healthcare workers"];
  let latestEntry = data[data.length - 1];
  let values = [
    latestEntry.HealthcareWorkersCovidCases,
    latestEntry.TotalConfirmedCovidCases -
      latestEntry.TotalCovidDeaths -
      latestEntry.TotalCovidRecovered -
      latestEntry.HealthcareWorkersCovidCases,
  ];

  return { labels, values };
};

export default function HealthCareChart(props) {
  const id = "healthcareChart";
  useEffect(() => {
    let { labels, values } = processData(props.data);

    new Chart(id, {
      type: "doughnut",
      data: {
        labels: labels,
        datasets: [
          {
            data: values,
            backgroundColor: ["rgba(255, 80, 80, 1)", "rgba(0, 120, 255, 1)"],
          },
        ],
      },
    });
  }, []);
  return (
    <>
      <Toolbar variant="dense">
        <h2 style={{ margin: "auto", textAlign: "center" }}>
          Healthcare worker infection rate
        </h2>
      </Toolbar>
      <div style={{ height: "240px", marginTop: "10px" }}>
        <canvas id={id} />
      </div>
    </>
  );
}
