import React from "react";

import { Grid, Paper } from "@material-ui/core";

import TransmissionChart from "../charts/TransmissionChart";
import GenderChart from "../charts/GenderChart";
import HealthCareChart from "../charts/HealthCareChart";
import HospitalChart from "../charts/HospitalChart";

export default function(props) {
  let { dailyData } = props;
  return (
    <>
      <Grid item xs={3}>
        <Paper className="ratioContainer">
          {dailyData && <TransmissionChart data={dailyData} />}
        </Paper>
      </Grid>
      <Grid item xs={3}>
        <Paper className="ratioContainer">
          {dailyData && <GenderChart data={dailyData} />}
        </Paper>
      </Grid>
      <Grid item xs={3}>
        <Paper className="ratioContainer">
          {dailyData && <HealthCareChart data={dailyData} />}
        </Paper>
      </Grid>
      <Grid item xs={3}>
        <Paper className="ratioContainer">
          {dailyData && <HospitalChart data={dailyData} />}
        </Paper>
      </Grid>
    </>
  );
}
