import React from "react";

import { Grid, Paper } from "@material-ui/core";

import NumberCard from "../NumberCard";

export default function(props) {
  let latest = props.dailyData[props.dailyData.length - 1];
  let delta = props.dailyData[props.dailyData.length - 2];
  return (
    <>
      <Grid item xs={4}>
        <NumberCard
          title="Total Cases"
          number={latest.TotalConfirmedCovidCases}
          delta={
            latest.TotalConfirmedCovidCases - delta.TotalConfirmedCovidCases
          }
        />
      </Grid>
      <Grid item xs={4}>
        <NumberCard
          title="Total Deaths"
          number={latest.TotalCovidDeaths}
          delta={latest.TotalCovidDeaths - delta.TotalCovidDeaths}
        />
      </Grid>
      <Grid item xs={4}>
        <NumberCard title="Tests Pending" number={latest.UnderInvestigation} />
      </Grid>
    </>
  );
}
