import React, { useEffect } from "react";
import { Toolbar } from "@material-ui/core";
import Chart from "chart.js";

const processData = (data) => {
  let labels = ["Active", "Hospitalised", "ICU"];

  let latestEntry = data[data.length - 1];
  let values = [
    latestEntry.TotalConfirmedCovidCases -
      latestEntry.TotalCovidDeaths -
      latestEntry.TotalCovidRecovered,
    latestEntry.HospitalisedCovidCases,
    latestEntry.RequiringICUCovidCases,
  ];

  return { labels, values };
};

export default function CountyChart(props) {
  let id = "hospitalChart";
  useEffect(() => {
    let { labels, values } = processData(props.data);

    new Chart(id, {
      type: "bar",
      data: {
        labels: labels,
        datasets: [
          {
            labels: labels,
            data: values,
            backgroundColor: [
              "rgba(0, 120, 255, 0.4)",
              "rgba(255, 0, 255, 0.4)",
              "rgba(255, 0, 0, 0.4)",
            ],
            borderColor: [
              "rgba(0, 120, 255, 0.8)",
              "rgba(255, 0, 255, 0.8)",
              "rgba(255, 0, 0, 0.8)",
            ],
            borderWidth: 2,
          },
        ],
      },
      options: {
        legend: {
          display: false,
        },
      },
    });
  }, []);
  return (
    <>
      <Toolbar variant="dense">
        <h2 style={{ margin: "auto" }}>Hospitalised Cases</h2>
      </Toolbar>
      <div style={{ height: "240px", marginTop: "10px" }}>
        <canvas id={id} />
      </div>
    </>
  );
}
