import React, { useEffect } from "react";
import { Toolbar } from "@material-ui/core";
import Chart from "chart.js";

const processData = (data) => {
  let labels = ["Male", "Female", "Unknown"];
  let latestEntry = data[data.length - 1];
  let values = [latestEntry.Male, latestEntry.Female, latestEntry.Unknown];

  return { labels, values };
};

export default function GenderChart(props) {
  const id = "genderChart";
  useEffect(() => {
    let { labels, values } = processData(props.data);

    new Chart(id, {
      type: "pie",
      data: {
        labels: labels,
        datasets: [
          {
            label: "# of cases per gender",
            data: values,
            backgroundColor: [
              "rgba(0, 120, 255, 0.9)",
              "rgba(255, 80, 80, 0.9)",
              "rgba(120, 200, 120, 0.9)",
            ],
          },
        ],
      },
    });
  }, []);
  return (
    <>
      <Toolbar variant="dense">
        <h2 style={{ margin: "auto" }}>Gender breakdown</h2>
      </Toolbar>
      <div style={{ height: "240px", marginTop: "10px" }}>
        <canvas id={id} />
      </div>
    </>
  );
}
