import React, { useEffect } from "react";

import Chart from "chart.js";
import { Toolbar } from "@material-ui/core";

const processData = (data) => {
  let totalCases = [];
  let totalDeaths = [];
  let totalRecoveries = [];
  data.forEach((item) => {
    // active cases = total - deaths - recovered
    totalCases.push({ x: item.Date, y: item.TotalConfirmedCovidCases });
    totalDeaths.push({ x: item.Date, y: item.TotalCovidDeaths });
    totalRecoveries.push({ x: item.Date, y: item.TotalCovidRecovered });
  });
  return { totalCases, totalDeaths, totalRecoveries };
};

export default function CasesChart(props) {
  let id = "casesChart";
  useEffect(() => {
    let { totalCases, totalDeaths, totalRecoveries } = processData(props.data);

    new Chart(id, {
      type: "line",
      data: {
        datasets: [
          {
            label: "# of Total Cases",
            data: totalCases, //[12, 19, 3, 5, 2, 3]
            backgroundColor: "rgba(0, 120, 255, 0.2)",
            borderColor: "rgba(0, 120, 255, 0.5)",
            pointBackgroundColor: "rgb(0, 120, 255)",
            pointHoverRadius: 5,
          },
          {
            label: "# of Confirmed Deaths",
            data: totalDeaths, //[12, 19, 3, 5, 2, 3]
            backgroundColor: "rgba(255, 0, 0, 0.2)",
            borderColor: "rgba(255, 0, 0, 0.5)",
            pointBackgroundColor: "rgb(255, 0, 0)",
            pointHoverRadius: 5,
          },
          {
            label: "# of Confirmed Recoveries",
            data: totalRecoveries, //[12, 19, 3, 5, 2, 3]
            backgroundColor: "rgba(0, 200, 0, 0.2)",
            borderColor: "rgba(0, 200, 0, 0.5)",
            pointBackgroundColor: "rgb(0, 200, 0)",
            pointHoverRadius: 5,
          },
        ],
      },
      options: {
        tooltips: {
          mode: "index",
        },
        scales: {
          xAxes: [
            {
              type: "time",
              time: {
                unit: "month",
              },
            },
          ],
        },
      },
    });
  });
  return (
    <>
      <Toolbar variant="dense">
        <h2 style={{ margin: "auto" }}>Cases over time</h2>
      </Toolbar>
      <div style={{ height: "340px", marginTop: "10px" }}>
        <canvas id={id} />
      </div>
    </>
  );
}
